<template>
  <div class="layout">
    <vx-header :showback="1" :showsave="1" :title="$t('common.superSet')" @save="save"></vx-header>
    <div class="layout-content">
      <div class="masks">
        <div class="masks-title">亮度调节</div>
        <div class="masks-contents">
          <vx-progress v-for="(item, index) in colors" :key="index" :title="item.title" :range="item.range" :step="item.step" :num="item.value" @update="val=>item.value=val"></vx-progress>
        </div>
      </div>
      <!--  <div class="masks" style="margin-top:.30rem">
        <div class="masks-contents">
          <vx-progress v-for="item in colorheight" :title="item.title" :range="item.range" :step="item.step" :num="item.value" @update="val=>item.value=val"></vx-progress>
        </div>
      </div> -->
    </div>
  </div>

</template>
<script>
  import { serialize } from 'src/utils';
  import htp from "../../api/http";
  import url from '../../api/apiurl';
  export default {
    created() {
      // this.colors = serialize(this.color);
      this.formatWinQuality();
    },
    methods: {
      formatWinQuality() {
        let ids = {};
        ids.selectWindow = this.winid;
        htp.post(url.getWinQuality, ids).then(res => {
          if(res.status === 10000000) {
            let data = res.data;
            let arr = [{}, {}, {}, {}, {}, {}, {}];
            this.colors = arr;
            this.winId = data.windowId;
          }
        });
      },
      save() {
        let qualityData = {
          colors: this.colors
        };
        this.$emit('save-quality', qualityData);
      }
    },
    props: ['show', 'color', 'winid'],
    data() {
      return {
        titles: {
          lightChange: "亮度调节"
        },
        colors: [],
        winId: ''
      };
    }
  };
</script>
<style scoped lang="less"></style>


